 { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { IRouter } from \"../../../../domain/presentation/IRouter\";\nimport { IBackofficeUseCase } from \"../../../../domain/useCase/IBackofficeUseCase\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IOrganizationUseCase } from \"../../../../domain/useCase/IOrganizationUseCase\";\nimport OrganizationList from \"../../components/backoffice/organization/organizationList.vue\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\n\n\nconst router = container.resolve<IRouter>(IRouter);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst backofficeUseCase = container.resolve<IBackofficeUseCase>(IBackofficeUseCase);\n\n\nconst routerParams = router.getRouteParams<{ userId: string }>();\nbackofficeUseCase.setBreadcrumb([\"Users\", \"User Detail\"]);\nconst user = ref<IUser_SUSI>();\nconst orgInfo = ref<Array<IOrganization>>([]);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst organizationsDataIsReady = ref(false);\n\n\n\nonBeforeMount(() => {\n    getOrganizations();\n})\n// function viewOrganization(organizationId: string) {\n//     router.push(`/backoffice/organization/${organizationId}`);\n// }\n// function remove( userId: string, organizationId: string) {\n//     executor.execute(async () => {\n//         await backofficeUseCase.removeUserFromOrganization({ userId, organizationId });\n//         // await getOrganizations();\n//     }, { loading: true });\n// }\n// function formatDate(date?: Date) {\n//     if(date) {\n//         const formatter = new Intl.DateTimeFormat('en-GB', { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric' });\n//         const formattedDate = formatter.format(new Date(date));\n//         return formattedDate;\n//     }\n// }\nfunction openManageUserDialog () {\n  dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/dialogs/backoffice/manageUser.vue`)),\n        { closable: true, title: \"Manage User\", width: \"499px\", maxHeight: \"184px\" }, {user: user.value}\n    );\n}\nfunction getOrganizations() {\n    executor.execute(async () => {\n        user.value = await backofficeUseCase.getUserInfo(routerParams.userId);\n        orgInfo.value = await backofficeUseCase.getOrganizationsByUserId(routerParams.userId);\n        organizationsDataIsReady.value = true;\n    }, { loading: true });\n}\n\nfunction openDeleteUserDialog() {\n    dialog.showDialog(\n            defineAsyncComponent(() => import(`../../components/dialogs/backoffice/deleteConfirmation.vue`)),\n            { closable: true, title: \"Delete User\", width: \"577px\", maxHeight: \"206px\" },\n            { \n                infoText: 'User Name',  \n                placeholder:`Write user name '${user.value?.name}' to delete`, validText: user.value?.name, \n                deleteCb: async () => {\n                    await executor.execute(async () => {\n                        await backofficeUseCase.deleteUser(user.value?.id!, user.value?.name!);\n                        router.push(\"/backoffice/users\");\n                    }, { loading: true });\n                }\n            });\n}\n</script>\n\n<template>\n    <div class=\"manage-user-div\">\n        <button class=\"manage-btn\" @click=\"openManageUserDialog()\"><i class=\"mdi mdi-cog\" style=\"font-size: 20px; margin-right: 4px\"></i><span>MANAGE USER</span></button>\n        <button class=\"delete-user-btn\" @click=\"openDeleteUserDialog()\"><i class=\"mdi mdi-delete\" style=\"font-size: 20px; margin-right: 4px\"></i><span>DELETE</span></button>\n    </div>  \n    <div style=\"display:flex; margin-bottom: 32px;\">\n        <div class=\"user-info-type\">\n            <div class=\"user-info-type-name\">Name</div>\n            <div class=\"user-info\">{{ user?.name }}</div>\n        </div>\n        <div class=\"user-info-type\">\n            <div class=\"user-info-type-name\">Last Name</div>\n            <div class=\"user-info\">{{ user?.lastName }}</div>\n        </div>\n        <div class=\"user-info-type\">\n            <div class=\"user-info-type-name\">E-mail</div>\n            <div class=\"user-info\">{{ user?.email }}</div>\n        </div>\n        <div class=\"user-info-type\" style=\"flex: 1\">\n            <div class=\"user-info-type-name\">ID</div>\n            <div class=\"user-info\" style=\"font-size: 20px;\">{{ user?.id }}</div>\n        </div>\n        <div class=\"user-info-type\" style=\"flex: 1\">\n            <div class=\"user-info-type-name\">Object ID</div>\n            <div class=\"user-info\" style=\"font-size: 20px;\">{{ user?.objectId }}</div>\n        </div>\n    </div>\n    <div style=\"margin-bottom: 32px;\">\n        <div v-if=\"organizationsDataIsReady\">\n            <OrganizationList :organizations=\"orgInfo\" :userID = \"user?.id\" @refreshTable=\"getOrganizations\"></OrganizationList>\n        </div>\n    </div>\n    <div style=\"display: none\">\n        <div class=\"div-title\">Roles ({{ user?.organizationIds.length }})</div>\n        <div>\n            <table class=\"user-org-table\"> \n                <tr class=\"thead\">\n                    <th class=\"col-xl-3\">ID</th>\n                    <th class=\"col-xl-3\">Name</th>\n                    <th class=\"col-xl-3\">Date Created</th>\n                    <th class=\"col-xl-2\">Date Joined</th>\n                    <th class=\"col-xl-1\"></th>\n                </tr>\n                <tr v-for=\"org in orgInfo\" :key=\"org.id\">\n                    <td>{{ org.id }}</td>\n                    <td>{{ org.name }}</td>\n                    <td> {{ org.createDate }}</td>\n                    <td>-</td>\n                    <td><div style=\"display: flex;\">\n                        <button class=\"remove-btn\">REMOVE</button>\n                        <button class=\"view-btn\">VIEW</button>\n                    </div></td>\n                </tr>\n            </table>\n        </div>\n    </div>\n\n</template>\n\n<style scoped>\n.user-info-type {\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n    border-radius: 10px;\n    background: #FFFFFF;\n    margin-right: 37px;\n    padding: 24px 24px 18px;\n    display: flex;\n    flex-direction: column;\n}\n.user-info-type:last-child {\n    margin-right: 0;\n}\n.user-info-type-name {\n    font-weight: 500;\n    font-size: 20px;\n    line-height: 23px;\n    color: #757575;\n    margin-bottom: 20px;\n    min-width: 100px;\n    flex: 1;\n}\n.user-info {\n    font-weight: 500;\n    font-size: 28px;\n    line-height: 33px;\n    color: #151233; \n}\n.div-title {\n    font-weight: 500;\n    font-size: 20px;\n    line-height: 23px;\n    color: #151233;\n    margin-bottom: 20px;\n}\n.user-org-table {\n    background: #FFFFFF;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n    border-radius: 10px;\n    width: 100%;\n}\ntd:hover {\n    background-color: none !important;\n}\ntable tr:hover {\n    background-color: #EEEEEE;\n}   \n.user-org-table th\n    {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 18px 36px\n}\n.user-org-table td {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 20px 36px;\n    color: #212121;\n    font-size: 14px;\n    line-height: 16px;\n}\n.user-org-table .thead {\n    background: #F5F5F5;\n    color: #616161;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n.view-btn {\n    background: none;\n    border:none;\n    color: #2C5A86;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n.manage-user-div {\n    width: 85%; \n    display: flex; \n    justify-content: end;\n    position: absolute;\n    top: 80px;\n    right: 36px;\n}\n.manage-btn {\n  border: 1px solid #151233;\n  border-radius: 4px;\n  padding: 1.5px 16px;\n  font-size: 15px;\n  color: #151233;\n  display: flex;\n  align-items: center;\n  font-weight: 500;\n}\n.delete-user-btn {\n        background: #CBCBCB;\n        border-radius: 4px;\n        color: #212121;\n        border: none;\n        margin-left: 12px;\n        display: flex;\n        align-items: center;\n        padding: 1.5px 16px;\n        font-size: 15px;\n        font-weight: 500;   \n    }\n</style>"],"sourceRoot":""}]);
